

































































































































































































































































































































































































































































































































































































































































































































































































































































































































































#app {
  width: 100vw;
  overflow: hidden;
  height: 100vh;
  color: #fff;
  background-color: #000;
  position: relative;
  background: url(~@/assets/daliuliang/map.png) center/contain no-repeat, url(~@/assets/daliuliang/bottom2.png) bottom/contain no-repeat,
    url(~@/assets/daliuliang/bg.png) center/cover no-repeat;
  .top-bar {
    font-family: "newFont";
    width: 100%;
    height: 112rem;
    text-align: center;
    line-height: 100rem;
    font-size: 40rem;
    background: url(~@/assets/daliuliang/top.png) center top/1630rem no-repeat;
  }
  .bottom-bar {
    width: 100%;
    position: absolute;
    bottom: 0;
    background: url(~@/assets/daliuliang/bottom.png) center bottom/1778rem no-repeat;
    height: 63rem;
    text-align: center;
    img {
      width: 92rem;
      margin-top: 31rem;
      cursor: pointer;
    }
  }
  .contain {
    width: 100%;
    display: flex;
    // align-items: center;
    justify-content: space-between;
  }
  .left-box {
    margin-left: 35rem;
    transform: translateY(-70rem);
  }
  .right-box {
    transform: translateY(-70rem);
    margin-right: 35rem;
    display: flex;
    flex-direction: column;
    .radar-chart {
      width: 350rem;
      height: 190rem;
    }
    .bar-chart {
      width: 350rem;
      height: 180rem;
    }
    .stop_car {
      font-size: 20rem;
      font-family: newFont;
      .label_title {
        margin-top: 10rem;
      }
      .stop_card {
        display: inline-block;
        font-size: 16rem;
        width: 165rem;
        height: 55rem;
        background: url(~@/assets/daliuliang/stop_card1.svg) center/contain no-repeat, url(~@/assets/daliuliang/stop_card2.svg) left/69rem 64rem no-repeat;
        .label_one {
          text-align: right;
          padding: 5rem 10rem 0 0;
        }
        .label_two {
          font-size: 18rem;
          text-align: right;
          padding: 0 10rem 0 0;
        }
      }
    }
  }
  .cards {
    display: flex;
    flex-wrap: wrap;
    width: 350rem;
    // height: 100rem;
    .card_small {
      width: 163rem;
      height: 55rem;
      padding: 5rem 0 0 20rem;
      background: url(~@/assets/daliuliang/card_small.png) center/163rem 55rem no-repeat;
      .row-one {
        font-size: 12rem;
      }
      .row-two {
        font-family: newFont;
        font-size: 22rem;
      }
    }
    .card_big {
      padding: 5rem 0 0 20rem;
      width: 163rem;
      height: 95rem;
      background: url(~@/assets/daliuliang/card_big.png) center/163rem 105rem no-repeat;
      .row-one {
        font-size: 12rem;
      }
      .row-two {
        font-family: newFont;
        font-size: 22rem;
      }
      .row-three {
        font-size: 12rem;
        .row-three-one,
        .row-three-two {
          display: flex;
          align-items: center;
        }
        .icon1_img {
          display: inline-block;
          width: 14rem;
          height: 14rem;
          background: url(~@/assets/daliuliang/icon1.png) center/25rem no-repeat;
        }
        .red-num {
          color: #e57800;
        }
        .blue-num {
          color: #00a3e5;
        }
      }
    }
  }
  .title_oneself {
    font-family: newFont;
    font-size: 22rem;
    padding-left: 8rem;
  }
  .line-charts1,
  .line-charts2,
  .line-charts3 {
    width: 320rem;
    height: 120rem;
    margin-bottom: 10rem;
  }
  .table_move {
    width: 350rem;
    padding-right: 26rem;
    .table_one {
      margin: 10rem 0;
      display: flex;
      align-items: center;
      justify-content: space-between;
      .btns {
        display: flex;
        .btn1,
        .btn2 {
          margin-right: 10rem;
          width: 56rem;
          height: 25rem;
          line-height: 25rem;
          text-align: center;
          cursor: pointer;
          background: url(~@/assets/daliuliang/btn1.png) center/contain no-repeat;
        }
        .isAcitve {
          background: url(~@/assets/daliuliang/btn2.png) center/contain no-repeat;
        }
      }
      .migration {
        padding: 5rem 15rem;
        background-color: rgba(18, 137, 221, 0.14);
        span {
          font-family: newFont;
          padding-left: 5rem;
        }
      }
    }
    .table_two {
      overflow: hidden;
      height: 180rem;
      .table_th {
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 30rem;
        & > div {
          text-align: center;
        }
      }
      .roll-box {
        position: relative;
        overflow: hidden;
        height: 150rem;
        .table_td {
          display: flex;
          align-items: center;
          justify-content: space-between;
          height: 30rem;

          & > div {
            text-align: center;
          }
          .td_one {
            img {
              width: 15rem;
              margin-right: 10rem;
            }
          }
        }
        .table_td:nth-of-type(even) {
          background: rgba(25, 214, 255, 0.1);
        }
        #box1 {
          position: absolute;
          width: 100%;
        }
        .animation-stop {
          animation-play-state: paused !important;
        }
        .animation-start {
          animation-play-state: running !important;
        }
      }
    }
  }
  .alarm-warning {
    padding-left: 50rem;
    position: absolute;
    top: 135rem;
    left: 50%;
    width: 580rem;
    height: 80rem;
    transform: translateX(-50%);
    font-size: 28rem;
    line-height: 28rem;
    background: url(~@/assets/daliuliang/alarm.png) center/580rem 80rem no-repeat;
    display: flex;
    align-items: center;
  }
  .tooltip_item {
    position: absolute;
    left: 1000rem;
    top: 460rem;
    width: 260rem;
    height: 150rem;
    background: url(~@/assets/daliuliang/alarm_tooltip.png) top right/252rem 140rem no-repeat, url(~@/assets/daliuliang/map_icon1.png) bottom left/30rem no-repeat,
      url(~@/assets/daliuliang/map_icon2.png) bottom left/30rem no-repeat;
    padding: 15rem 0 0 40rem;
    .icon-row {
      display: flex;
      align-items: center;

      .icon_alarm {
        width: 30rem;
        height: 30rem;
        background: url(~@/assets/daliuliang/warning_icon2.svg) center/40rem no-repeat;
      }
      span {
        font-size: 18rem;
      }
    }
    .row-two {
      font-size: 22rem;
      color: #ff9c73;
    }
    .row-three,
    .row-four {
      font-size: 12rem;
    }
  }
}
@font-face {
  font-family: "newFont";
  src: url("./assets/daliuliang/newFont.ttf");
}
@keyframes movetop1 {
  from {
    top: 0;
  }
  to {
    top: 0;
  }
}
