#app {
  width: 100vw;
  overflow: hidden;
  height: 100vh;
  color: #fff;
  background-color: #000;
  position: relative;
  background: url(~@/assets/daliuliang/map.png) center / contain no-repeat, url(~@/assets/daliuliang/bottom2.png) bottom / contain no-repeat, url(~@/assets/daliuliang/bg.png) center / cover no-repeat;
}
#app .top-bar {
  font-family: "newFont";
  width: 100%;
  height: 112rem;
  text-align: center;
  line-height: 100rem;
  font-size: 40rem;
  background: url(~@/assets/daliuliang/top.png) center top / 1630rem no-repeat;
}
#app .bottom-bar {
  width: 100%;
  position: absolute;
  bottom: 0;
  background: url(~@/assets/daliuliang/bottom.png) center bottom / 1778rem no-repeat;
  height: 63rem;
  text-align: center;
}
#app .bottom-bar img {
  width: 92rem;
  margin-top: 31rem;
  cursor: pointer;
}
#app .contain {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
#app .left-box {
  margin-left: 35rem;
  transform: translateY(-70rem);
}
#app .right-box {
  transform: translateY(-70rem);
  margin-right: 35rem;
  display: flex;
  flex-direction: column;
}
#app .right-box .radar-chart {
  width: 350rem;
  height: 190rem;
}
#app .right-box .bar-chart {
  width: 350rem;
  height: 180rem;
}
#app .right-box .stop_car {
  font-size: 20rem;
  font-family: newFont;
}
#app .right-box .stop_car .label_title {
  margin-top: 10rem;
}
#app .right-box .stop_car .stop_card {
  display: inline-block;
  font-size: 16rem;
  width: 165rem;
  height: 55rem;
  background: url(~@/assets/daliuliang/stop_card1.svg) center / contain no-repeat, url(~@/assets/daliuliang/stop_card2.svg) left / 69rem 64rem no-repeat;
}
#app .right-box .stop_car .stop_card .label_one {
  text-align: right;
  padding: 5rem 10rem 0 0;
}
#app .right-box .stop_car .stop_card .label_two {
  font-size: 18rem;
  text-align: right;
  padding: 0 10rem 0 0;
}
#app .cards {
  display: flex;
  flex-wrap: wrap;
  width: 350rem;
}
#app .cards .card_small {
  width: 163rem;
  height: 55rem;
  padding: 5rem 0 0 20rem;
  background: url(~@/assets/daliuliang/card_small.png) center / 163rem 55rem no-repeat;
}
#app .cards .card_small .row-one {
  font-size: 12rem;
}
#app .cards .card_small .row-two {
  font-family: newFont;
  font-size: 22rem;
}
#app .cards .card_big {
  padding: 5rem 0 0 20rem;
  width: 163rem;
  height: 95rem;
  background: url(~@/assets/daliuliang/card_big.png) center / 163rem 105rem no-repeat;
}
#app .cards .card_big .row-one {
  font-size: 12rem;
}
#app .cards .card_big .row-two {
  font-family: newFont;
  font-size: 22rem;
}
#app .cards .card_big .row-three {
  font-size: 12rem;
}
#app .cards .card_big .row-three .row-three-one,
#app .cards .card_big .row-three .row-three-two {
  display: flex;
  align-items: center;
}
#app .cards .card_big .row-three .icon1_img {
  display: inline-block;
  width: 14rem;
  height: 14rem;
  background: url(~@/assets/daliuliang/icon1.png) center / 25rem no-repeat;
}
#app .cards .card_big .row-three .red-num {
  color: #e57800;
}
#app .cards .card_big .row-three .blue-num {
  color: #00a3e5;
}
#app .title_oneself {
  font-family: newFont;
  font-size: 22rem;
  padding-left: 8rem;
}
#app .line-charts1,
#app .line-charts2,
#app .line-charts3 {
  width: 320rem;
  height: 120rem;
  margin-bottom: 10rem;
}
#app .table_move {
  width: 350rem;
  padding-right: 26rem;
}
#app .table_move .table_one {
  margin: 10rem 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
#app .table_move .table_one .btns {
  display: flex;
}
#app .table_move .table_one .btns .btn1,
#app .table_move .table_one .btns .btn2 {
  margin-right: 10rem;
  width: 56rem;
  height: 25rem;
  line-height: 25rem;
  text-align: center;
  cursor: pointer;
  background: url(~@/assets/daliuliang/btn1.png) center / contain no-repeat;
}
#app .table_move .table_one .btns .isAcitve {
  background: url(~@/assets/daliuliang/btn2.png) center / contain no-repeat;
}
#app .table_move .table_one .migration {
  padding: 5rem 15rem;
  background-color: rgba(18, 137, 221, 0.14);
}
#app .table_move .table_one .migration span {
  font-family: newFont;
  padding-left: 5rem;
}
#app .table_move .table_two {
  overflow: hidden;
  height: 180rem;
}
#app .table_move .table_two .table_th {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 30rem;
}
#app .table_move .table_two .table_th > div {
  text-align: center;
}
#app .table_move .table_two .roll-box {
  position: relative;
  overflow: hidden;
  height: 150rem;
}
#app .table_move .table_two .roll-box .table_td {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 30rem;
}
#app .table_move .table_two .roll-box .table_td > div {
  text-align: center;
}
#app .table_move .table_two .roll-box .table_td .td_one img {
  width: 15rem;
  margin-right: 10rem;
}
#app .table_move .table_two .roll-box .table_td:nth-of-type(even) {
  background: rgba(25, 214, 255, 0.1);
}
#app .table_move .table_two .roll-box #box1 {
  position: absolute;
  width: 100%;
}
#app .table_move .table_two .roll-box .animation-stop {
  animation-play-state: paused !important;
}
#app .table_move .table_two .roll-box .animation-start {
  animation-play-state: running !important;
}
#app .alarm-warning {
  padding-left: 50rem;
  position: absolute;
  top: 135rem;
  left: 50%;
  width: 580rem;
  height: 80rem;
  transform: translateX(-50%);
  font-size: 28rem;
  line-height: 28rem;
  background: url(~@/assets/daliuliang/alarm.png) center / 580rem 80rem no-repeat;
  display: flex;
  align-items: center;
}
#app .tooltip_item {
  position: absolute;
  left: 1000rem;
  top: 460rem;
  width: 260rem;
  height: 150rem;
  background: url(~@/assets/daliuliang/alarm_tooltip.png) top right / 252rem 140rem no-repeat, url(~@/assets/daliuliang/map_icon1.png) bottom left / 30rem no-repeat, url(~@/assets/daliuliang/map_icon2.png) bottom left / 30rem no-repeat;
  padding: 15rem 0 0 40rem;
}
#app .tooltip_item .icon-row {
  display: flex;
  align-items: center;
}
#app .tooltip_item .icon-row .icon_alarm {
  width: 30rem;
  height: 30rem;
  background: url(~@/assets/daliuliang/warning_icon2.svg) center / 40rem no-repeat;
}
#app .tooltip_item .icon-row span {
  font-size: 18rem;
}
#app .tooltip_item .row-two {
  font-size: 22rem;
  color: #ff9c73;
}
#app .tooltip_item .row-three,
#app .tooltip_item .row-four {
  font-size: 12rem;
}
@font-face {
  font-family: "newFont";
  src: url("./assets/daliuliang/newFont.ttf");
}
@keyframes movetop1 {
  from {
    top: 0;
  }
  to {
    top: 0;
  }
}
