* {
  box-sizing: border-box;
}

html,
body {
  min-height: 100%;
}

body {
  font-family: "Microsoft YaHei";
  /* font-size: 14px; */
  /* color: #333; */
}

/* h1, */

h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
}

ul,
ol {
  list-style: none;
}

img {
  border: none;
  vertical-align: middle;
}

a {
  text-decoration: none;
  color: #232323;
}

table {
  border-collapse: collapse;
  table-layout: fixed;
}

input,
textarea {
  outline: none;
  /* border: none; */
}

textarea {
  resize: none;
  overflow: auto;
}

.clearfix {
  zoom: 1;
}

.clearfix:after {
  content: ".";
  width: 0;
  height: 0;
  visibility: hidden;
  display: block;
  clear: both;
  overflow: hidden;
}

.fl {
  float: left;
}

.fr {
  float: right;
}

.tl {
  text-align: left;
}

.tc {
  text-align: center;
}

.tr {
  text-align: right;
}

.ellipse {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.inline {
  display: inline-block;
  *display: inline;
  *zoom: 1;
}
/* 键盘元素标签<kbd> */
kbd {
  background: linear-gradient(180deg, #eee, #fff);
  background-color: #eee;
  border: 1px solid #cdd5d7;
  border-radius: 6px;
  box-shadow: 0 1px 2px 1px #cdd5d7;
  font-family: consolas, "Liberation Mono", courier, monospace;
  font-size: 0.9rem;
  font-weight: 700;
  line-height: 1;
  margin: 3px;
  padding: 4px 6px;
  white-space: nowrap;
}
/* 滚动条宽度 */
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}
/* 滚动条(条部分) */
::-webkit-scrollbar-thumb {
  border-radius: 3px;
  /* background-color: rgba(144, 147, 153, 0.5); */
  background-color: rgba(144, 147, 153, 0.5);
}
/* 滚动条(导轨部分) */
::-webkit-scrollbar-track {
  background-color: transparent;
}
/* textarea 右下角拉伸样式 */
::-webkit-resizer {
  background-color: transparent;
}
/* textarea 出现滚动条时,右下角一个方框 */
::-webkit-scrollbar-corner {
  background-color: transparent;
}
/* prsimjs样式 */
.line-numbers {
  padding: 0 !important;
}

